import React from "react";
import { useState, useEffect } from "react";
import { useHistory } from "react-router";
import {
  Button,
  Container,
  Grid,
  TextField,
  Typography,
  makeStyles,
  ButtonGroup,
  Paper,
  Select,
  MenuItem
} from "@material-ui/core";
import axios from "axios";

const useStyles = makeStyles((theme) => ({
  title: {
    margin: "30px 0px 30px 0px",
  },
  backdrop: {
    zIndex: theme.zIndex.drawer + 1,
    color: "#fff",
  },
  nameField: {
    width: '241px',
    paddingLeft: '41px'
  },
  typeField: {
    width: '120px',
    marginLeft: '41px'
  },
  pointField: {
    width: '71px',
    paddingLeft: '41px'
  },
  contentGrid: {
    width: '700px',
    margin: 'auto'
  },
  inputLabel: {
    padding: '6px 0 7px'
  },
  gridItem: {
    marginBottom: '20px'
  }
}));

const App = (props) => {
  const classes = useStyles();
  const history = useHistory();

  const [keyword, setKeyword] = useState(props.initialKeyword);
  const [types, setTypes] = useState();
  const [titleOption, setTitleOption] = useState(false);
  const [keywordMessage, setKeywordMessage] = useState("");
  const [typeMessage, setTypeMessage] = useState("");

  useEffect(() => {
    setTitleOption(keyword.keyword);

    axios.get("http://127.0.0.1:8000/api/auth/types")
      .then(res => {
        setTypes(res.data.objType);
      })
      .catch(err => {
        console.log(err);
      })
  }, [])

  const handleKeywordChange = (e) => {
    setKeyword({ ...keyword, keyword: e.target.value });
  }

  const handlePointsChange = (e) => {
    setKeyword({ ...keyword, points: e.target.value });
  }

  const handleTypeChange = (e) => {
    setKeyword({ ...keyword, type: e.target.value })
  }

  const handleCancel = (e) => {
    history.push("/apps/edit/keywords");
  }

  // Validation
  const handleSave = (e) => {
    let errors = 0;

    if (!keyword.keyword) {
      setKeywordMessage("Required");
      errors++;
    }

    if (keyword.keyword.length > 200) {
      setKeywordMessage("Cannot exceed 200 characters");
      errors++;
    }

    if (!keyword.type) {
      setTypeMessage("Required");
      errors++;
    }

    if (errors === 0) {
      props.onPost(keyword);
    }
  }

  // Enables enter key
  // useEffect(() => {
  //   function handleKeyDown(e) {
  //     if (e.keyCode === 13) {
  //       handleSave();
  //     }
  //   }

  //   document.addEventListener('keydown', handleKeyDown);

  //   return function cleanup() {
  //     document.removeEventListener('keydown', handleKeyDown);
  //   }
  // }, []);

  return (
    <Container>
      <Paper style={{ paddingBottom: '50px', marginBottom: '50px', marginTop: '24px' }}>
        <div
          style={{
            display: "flex",
            justifyContent: "space-around",
            backgroundColor: '#64B5F6',
            marginBottom: '40px',
            height: '100px',
            borderTopLeftRadius: '4px',
            borderTopRightRadius: '4px'
          }}>
          <h1
            style={{ margin: 'auto' }}
          >
            {props.type === "Edit" ? `Edit Keyword: ${titleOption}` : `Create Keyword`}
          </h1>
        </div>

        <Grid container className={classes.contentGrid}>
          <Grid item xs={3} className={classes.gridItem}>
            <Typography className={classes.inputLabel}>Keyword:</Typography>
          </Grid>
          <Grid item xs={9} className={classes.gridItem}>
            <TextField
              autoFocus
              fullWidth
              value={keyword.keyword}
              onChange={handleKeywordChange}
              error={keywordMessage !== ""}
              helperText={keywordMessage}
            />
          </Grid>
          <Grid item xs={3} className={classes.gridItem}>
            <Typography className={classes.inputLabel}>Type:</Typography>
          </Grid>
          <Grid item xs={9} className={classes.gridItem}>
            <Select
              fullWidth
              value={keyword.type}
              onChange={handleTypeChange}
              error={typeMessage !== ""}
              helperText={typeMessage}
            >
              {!types ? null :
                types.map(({ id, type }) => (
                  <MenuItem key={id} value={id}>{type}</MenuItem>
                ))}
            </Select>
          </Grid>
          <Grid item xs={3} className={classes.gridItem}>
            <Typography className={classes.inputLabel}>Points:</Typography>
          </Grid>
          <Grid item xs={9} className={classes.gridItem}>
            <TextField
              fullWidth
              type="number"
              value={keyword.points}
              onChange={handlePointsChange}
              InputProps={{ inputProps: { min: 0 } }}
            >
            </TextField>
          </Grid>
          <Grid item xs={4} style={{ textAlign: "left" }}>
            <ButtonGroup size="small" variant="contained" style={{ marginTop: '20px' }}>
              <Button onClick={handleSave} color='primary'>{props.type === "Edit" ? "Save" : "Create"}</Button>
              <Button onClick={handleCancel} color='secondary'>Cancel</Button>
            </ButtonGroup>
          </Grid>
        </Grid>
      </Paper>
    </Container>
  );
};

export default App;
